import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {buttons} from './buttons.json'
import CalcButton from './CalcButton'
import Calculator from './Calculator'
import * as math from 'mathjs'

class App extends Component {
  constructor() {
    super()
    this.state = {
      display: "0",
      buttonsData: buttons
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleClear = this.handleClear.bind(this)
    this.handleEquals = this.handleEquals.bind(this)
    this.handleZero = this.handleZero.bind(this)
    this.handleDecimals = this.handleDecimals.bind(this)
    this.handleOperators = this.handleOperators.bind(this)
    this.handleNumbers = this.handleNumbers.bind(this)
  }

  handleChange(event) {
    const {innerHTML} = event.target
    this.setState(prevState => {
      return {
      display: prevState.display + innerHTML
      }
    })
  }

  handleClear() {
    this.setState({display: "0"})
  }

  handleEquals() {
    const decimalRegex = /\.+/g
    if (decimalRegex.test(this.state.display)){
      this.setState(prevState => {
        return {display: prevState.display.replace(decimalRegex,'.')}
      })
    }
    this.setState(prevState => {
      return {display: math.eval(prevState.display)}
    })
  }

  handleZero(event) {
    const {innerHTML} = event.target
    if (this.state.display === "0"){
      this.setState({display: innerHTML})
    }
    else {
      this.setState(prevState => {
        return {display: prevState.display + innerHTML}
      })
    }
  }

  handleDecimals(event){
    const {innerHTML} = event.target
    const lastCharIsNum = /\d+$/

    if (this.state.display === "0") {
      this.setState({display: innerHTML})
    }
    else if (lastCharIsNum.test(this.state.display)){
      this.setState(prevState => {
        return {display: prevState.display + innerHTML}
      })
    }
    else {
      this.setState(prevState => {
        return {display: prevState.display}
      })
    }
  }

  handleOperators(event) {
    const {innerHTML} = event.target
  const lastCharIsOp = /[-*/+]$/
//Figure out why your operators and numbers are both using this method
    if (this.state.display === "0" || this.state.display === ".") {
        this.setState(prevState => {
          return {display: prevState.display}
        })
      alert("Pleae enter a number first.")
    }
    else if (lastCharIsOp.test(this.state.display)) {
      this.setState(prevState => {
        return {display: prevState.display.replace(lastCharIsOp,innerHTML)}
      })
    }
    else {
      this.setState(prevState => {
        return {display: prevState.display + innerHTML}
      })
    }
  }

  handleNumbers(event) {
    const {innerHTML} = event.target
      if (this.state.display === "0"){
        this.setState({display: innerHTML})
      }
      else {
        this.setState(prevState => {
          return {display: prevState.display + innerHTML}
        })
      }
  }

  //And now we render the UI
  render() {
    const calcButtons = this.state.buttonsData.map(currentButton => 
      <CalcButton 
        id = {currentButton.name}
        key = {currentButton.name}
        text = {currentButton.text}
        onClick = {currentButton.name === "clear" ? this.handleClear 
                  : currentButton.name === "equals" ? this.handleEquals 
                  : currentButton.name === "zero" ? this.handleZero
                  : currentButton.name === "decimal" ? this.handleDecimals
                  : currentButton.name === "add" ? this.handleOperators
                  : currentButton.name === "subtract" ? this.handleOperators
                  : currentButton.name === "multiply" ? this.handleOperators
                  : currentButton.name === "divide" ? this.handleOperators
                  : currentButton.name === "empty" ? () => {console.log("This button does nothing lol")}
                  : this.handleNumbers}
      />
      )
    return (
      <div>
        <Calculator
          logo = {logo}
          buttonComponents = {calcButtons}
          handleChange = {this.handleChange}
          displayText = {this.state.display}
        />
      </div>
    );
  }
}

export default App;
